@font-face {
  font-family: "Proxima Nova";
  src: local("proxima-nova-light"), url("../fonts/proxima-nova-light.woff2") format("woff2"), url("../fonts/proxima-nova-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: local("proxima-nova-regular"), url("../fonts/proxima-nova-regular.woff2") format("woff2"), url("../fonts/proxima-nova-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: local("proxima-nova-semibold"), url("../fonts/proxima-nova-semibold.woff2") format("woff2"), url("../fonts/montserrat-alternates-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Proxima Nova";
  src: local("proxima-nova-bold"), url("../fonts/proxima-nova-bold.woff2") format("woff2"), url("../fonts/proxima-nova-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "El Messiri";
  src: local("el-messiri-bold"), url("../fonts/el-messiri-bold.woff2") format("woff2"), url("../fonts/el-messiri-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
.page {
  height: 100%;
  scroll-behavior: smooth;
  background-color: #162831;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .page {
    padding: 0 10px;
  }
}

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
.page__body--hidden {
  overflow: hidden;
}

.header {
  flex: 0 0 auto;
  padding-top: 30px;
  padding: 30px 0;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 60px;
}
.header__wrapper--border {
  padding-bottom: 30px;
  border-bottom: 1px solid #ffffff;
}

@media (max-width: 767px) {
  .header__logo {
    width: 50px;
    height: 50px;
  }
}

.nav {
  display: flex;
  position: relative;
}
.nav--nojs + button {
  margin-right: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  width: 655px;
}
@media (max-width: 1023px) {
  .nav__list {
    flex-direction: column;
    justify-content: center;
    position: fixed;
    width: 0;
    height: 0;
    left: 0;
    top: 0;
    background-color: #018abe;
    z-index: 5;
    visibility: hidden;
    opacity: 0;
  }
  .nav__list--show {
    opacity: 1;
    transition: 0.3s ease-in-out;
    visibility: visible;
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 1023px) and (max-width: 1023px) {
  .nav__list--nojs {
    position: absolute;
    visibility: visible;
    flex-direction: row;
    width: 500px;
    height: 50px;
    left: -248px;
    top: -25px;
    right: 0;
    opacity: 1;
    background-color: transparent;
  }
}
@media (max-width: 1023px) {
  .nav__list--nojs .nav__item {
    margin-right: 20px;
  }
  .nav__list--nojs .nav__item:last-child {
    margin-right: 0;
  }
}

.nav__item {
  margin-right: 50px;
  margin-bottom: 5px;
  margin-top: 5px;
}
.nav__item:last-child {
  margin-right: 0;
}
@media (max-width: 1023px) {
  .nav__item {
    margin-right: 0;
  }
}
.nav__item a {
  font-family: "Proxima Nova", "Verdana", sans-serif;
  font-size: 14px;
  line-height: 19px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  padding-bottom: 2px;
  background-image: linear-gradient(currentColor, currentColor);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 0% 1px;
  transition: 0.3s ease-in-out;
}
.nav__item a:hover, .nav__item a:focus-visible {
  background-size: 100% 1px;
}

.main {
  flex: 1 0 auto;
  overflow: hidden;
  transition: 0.3s ease-in-out;
}

.intro {
  padding: 20px 0 75px;
}
@media (max-width: 767px) {
  .intro {
    padding-bottom: 35px;
  }
}

.intro__wrapper {
  background-image: url("../img/intro-bcg.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 600px;
  padding: 36px 20px;
  border-radius: 10px;
}
@supports not (background-image: url("../img/intro-bcg.webp")) {
  .intro__wrapper {
    background-image: url("../img/intro-bcg.png");
  }
}
@media (max-width: 767px) {
  .intro__wrapper {
    height: 250px;
  }
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .intro__wrapper {
    background-image: url("../img/intro-bcg@2x.webp");
    background-size: cover;
  }
  @supports not (background-image: url("../img/intro-bcg@2x.webp")) {
    .intro__wrapper {
      background-image: url("../img/intro-bcg@2x.png");
    }
  }
}
.intro__wrapper p,
.intro__wrapper h1 {
  margin: 0;
  color: #ffffff;
  text-align: center;
}
.intro__wrapper h1 {
  font-family: "El Messiri", "Arial", sans-serif;
  font-size: 64px;
  line-height: 90px;
  font-weight: 700;
  font-style: normal;
}
@media (max-width: 767px) {
  .intro__wrapper h1 {
    font-size: 36px;
    line-height: 42px;
  }
}
.intro__wrapper p {
  font-family: "El Messiri", "Arial", sans-serif;
  font-size: 24px;
  line-height: 34px;
  font-weight: 700;
  font-style: normal;
  position: relative;
  padding: 0 106px 40px;
}
@media (max-width: 767px) {
  .intro__wrapper p {
    font-size: 18px;
    line-height: 24px;
    padding: 0 30px 25px;
  }
}
.intro__wrapper p::after, .intro__wrapper p::before {
  position: absolute;
  content: url("../img/decor/decor-element-white.svg");
}
.intro__wrapper p::after {
  right: 78px;
  transform: rotate3d(0, 1, 0, 180deg);
}
@media (max-width: 767px) {
  .intro__wrapper p::after {
    right: 0;
  }
}
.intro__wrapper p::before {
  left: 80px;
}
@media (max-width: 767px) {
  .intro__wrapper p::before {
    left: 0;
  }
}

.intro__inner {
  width: 100%;
  max-width: 587px;
  margin: 100px auto;
}
@media (max-width: 767px) {
  .intro__inner {
    margin-top: 0;
  }
}

.sessions {
  padding: 75px 0 75px;
}
@media (max-width: 767px) {
  .sessions {
    padding: 45px 0 45px;
  }
}

.sessions__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr 1fr;
  gap: 24px;
  margin-top: 79px;
}
@media (max-width: 1023px) {
  .sessions__list {
    margin-top: 40px;
  }
}
@media (max-width: 767px) {
  .sessions__list {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}

.sessions__item {
  max-width: 416px;
  width: 100%;
  min-height: 258px;
  background: linear-gradient(0deg, rgba(1, 138, 190, 0.5), rgba(1, 138, 190, 0.5)), url("../img/about-img.png") no-repeat center/cover;
  border-radius: 10px;
  transition: 0.3s ease-in-out;
  padding: 0 40px 30px;
  display: flex;
}
@media (max-width: 767px) {
  .sessions__item {
    padding: 0 25px 30px;
  }
}
.sessions__item a {
  font-family: "El Messiri", "Arial", sans-serif;
  font-size: 24px;
  line-height: 34px;
  font-weight: 700;
  font-style: normal;
  color: #ffffff;
  margin-top: auto;
}
@media (max-width: 767px) {
  .sessions__item a {
    font-size: 18px;
    line-height: 28px;
  }
}
.sessions__item:first-child {
  min-height: 540px;
  grid-row-start: 1;
  grid-row-end: -1;
}
@media (max-width: 767px) {
  .sessions__item:first-child {
    min-height: 258px;
    grid-row: 1/1;
  }
}
.sessions__item:hover {
  transition: 0.3s ease-in-out;
  box-shadow: inset 0 -7px 0 0 #018abe;
}

.feedback {
  padding: 75px 0;
  max-width: 1826px;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
}
@media (max-width: 1023px) {
  .feedback {
    max-width: 735px;
  }
}
@media (max-width: 767px) {
  .feedback {
    padding-top: 45px;
  }
}

.feedback__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-top: 80px;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .feedback__list {
    margin-top: 40px;
  }
}

.feedback__item {
  min-width: 500px;
  min-height: 276px;
  background-color: #018abe;
  padding: 41px 45px 37px;
  opacity: 0.4;
  border-radius: 10px;
  margin-bottom: 61px;
}
@media (max-width: 1023px) {
  .feedback__item {
    min-width: 100%;
  }
}
@media (max-width: 767px) {
  .feedback__item {
    padding: 30px 15px 36px;
  }
}
.feedback__item blockquote {
  margin: 0;
}
.feedback__item blockquote p {
  margin: 0;
  font-family: "Proxima Nova", "Verdana", sans-serif;
  font-size: 10px;
  line-height: 14px;
  font-weight: 300;
  font-style: normal;
  color: #ffffff;
}
.feedback__item blockquote p:last-child {
  padding-top: 15px;
}

.feedback__user {
  color: #ffffff;
  margin: 0;
  font-family: "Proxima Nova", "Verdana", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  font-style: normal;
  padding-bottom: 14px;
}

.feedback__item--active {
  opacity: 1;
  transition: opacity 0.3s, visibility 0s linear 0.3s;
  min-width: 636px;
  min-height: 350px;
  padding: 50px 60px 56px;
}
@media (max-width: 1023px) {
  .feedback__item--active {
    min-width: 100%;
  }
}
@media (max-width: 767px) {
  .feedback__item--active {
    padding: 30px 15px 36px;
  }
}
.feedback__item--active .feedback__user {
  font-size: 20px;
  line-height: 28px;
  padding-bottom: 20px;
}
@media (max-width: 767px) {
  .feedback__item--active .feedback__user {
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 15px;
  }
}
.feedback__item--active blockquote p {
  font-size: 14px;
  line-height: 20px;
}
.feedback__item--active blockquote p:last-child {
  padding-top: 17px;
}

.feedback__pagination {
  width: 124px;
  min-height: 16px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.feedback__bullet {
  cursor: pointer;
  width: 16px;
  height: 16px;
  background-color: #ffffff;
  opacity: 0.4;
  border-radius: 3px;
}
.feedback__bullet--active {
  opacity: 1;
}

.price {
  padding: 55px 0;
}
@media (max-width: 767px) {
  .price {
    padding: 35px 0;
  }
}

.price__wrapper {
  display: flex;
  max-width: 1116px;
  padding: 0 20px;
  margin: 79px auto 0;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1023px) {
  .price__wrapper {
    margin-top: 50px;
  }
}
@media (max-width: 767px) {
  .price__wrapper {
    flex-direction: column;
    margin-top: 25px;
  }
}

.price__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  max-width: 416px;
  width: 100%;
  margin-right: 20px;
}
@media (max-width: 767px) {
  .price__list {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.price__item {
  display: flex;
  margin-bottom: 31px;
  justify-content: space-between;
}
.price__item:last-child {
  margin-bottom: 0;
}
.price__item p {
  margin: 0;
  color: #ffffff;
}
.price__item p:last-child {
  font-family: "El Messiri", "Arial", sans-serif;
  font-size: 18px;
  line-height: 25px;
  font-weight: 700;
  font-style: normal;
}
@media (max-width: 1023px) {
  .price__item p:last-child {
    font-size: 17px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .price__item p:last-child {
    font-size: 14px;
    line-height: 18px;
  }
}

.price__descr {
  position: relative;
  font-family: "Proxima Nova", "Verdana", sans-serif;
  font-size: 18px;
  line-height: 25px;
  font-weight: 400;
  font-style: normal;
  padding-left: 38px;
  padding-right: 15px;
  display: flex;
  align-items: center;
}
@media (max-width: 1023px) {
  .price__descr {
    font-size: 15px;
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .price__descr {
    font-size: 13px;
    line-height: 17px;
  }
}
.price__descr::before {
  position: absolute;
  content: "";
  width: 18px;
  height: 9px;
  background-image: url("../img/decor/decor-element-blue.svg");
  background-size: cover;
  background-repeat: no-repeat;
  left: 0;
}

.price__gift-card {
  margin: 0;
}
.price__gift-card figcaption {
  font-family: "El Messiri", "Arial", sans-serif;
  font-size: 21px;
  line-height: 29px;
  font-weight: 700;
  font-style: normal;
  color: #ffffff;
  text-align: center;
  margin-top: 30px;
}
@media (max-width: 767px) {
  .price__gift-card figcaption {
    font-size: 18px;
    line-height: 25px;
    margin-top: 15px;
  }
}

.about {
  padding: 75px 0;
}

.about__wrapper {
  display: flex;
  flex-direction: column;
  max-width: 1076px;
  margin: 82px auto 0;
}
@media (max-width: 1023px) {
  .about__wrapper {
    margin-top: 40px;
  }
}

.about__massage,
.about__hydrotherapy {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .about__massage,
.about__hydrotherapy {
    flex-direction: column;
  }
}

.about__massage {
  margin-bottom: 100px;
  align-items: center;
}
@media (max-width: 1023px) {
  .about__massage {
    margin-bottom: 50px;
  }
}

@media (max-width: 1023px) {
