  color: #ffffff;
}
.about__text h3 {
  font-family: "Proxima Nova", "Verdana", sans-serif;
  font-size: 24px;
  line-height: 34px;
  font-weight: 700;
  font-style: normal;
  padding-bottom: 30px;
}
@media (max-width: 1023px) {
  .about__text h3 {
    text-align: center;
  }
}
.about__text p {
  font-family: "Proxima Nova", "Verdana", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  font-style: normal;
  padding-bottom: 17px;
}

.about__hydrotherapy-img {
  order: -1;
}
@media (max-width: 1023px) {
  .about__hydrotherapy-img {
    order: 0;
  }
}

.about__hydrotherapy .about__text {
  padding-left: 30px;
  padding-top: 17px;
}
@media (max-width: 1023px) {
  .about__hydrotherapy .about__text {
    padding-left: 0;
  }
}
.about__hydrotherapy .about__text p {
  letter-spacing: -0.6px;
}

.contact {
  padding: 95px 0 75px;
  display: flex;
  justify-content: space-between;
  min-height: 604px;
}
@media (max-width: 1023px) {
  .contact {
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .contact {
    padding: 40px 0 45px;
  }
}
.contact iframe {
  border: none;
  width: 100%;
  border-radius: 10px;
  min-height: 604px;
}
@media (max-width: 767px) {
  .contact iframe {
    min-height: 480px;
  }
}

.contact__map {
  background-image: url("../img/bcg-map.png");
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 10px;
  max-width: 856px;
  width: 100%;
  height: 604px;
}
@media (max-width: 1023px) {
  .contact__map {
    margin-bottom: 20px;
    max-width: 100%;
  }
}
@media (max-width: 767px) {
