}
@media (max-width: 1023px) {
  .contact__boxes {
    flex-direction: row;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .contact__boxes {
    flex-direction: column;
  }
}

.contact__box {
  width: 392px;
  min-height: 290px;
  border-radius: 10px;
  background-color: #018abe;
  padding: 35px 45px;
}
@media (max-width: 1023px) {
  .contact__box {
    width: 100%;
    margin-right: 10px;
  }
  .contact__box:last-child {
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .contact__box {
    margin-bottom: 20px;
    margin-right: 0;
    padding: 35px 25px;
  }
}
.contact__box h3 {
  font-family: "El Messiri", "Arial", sans-serif;
  font-size: 24px;
  line-height: 34px;
  font-weight: 700;
  font-style: normal;
  padding-bottom: 20px;
  margin: 0;
  color: #ffffff;
}
@media (max-width: 767px) {
  .contact__box h3 {
    font-size: 20px;
    line-height: 28px;
  }
}

.contact__box-list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-bottom: 29px;
}

.contact__box-item {
  display: flex;
  margin-bottom: 21px;
}
.contact__box-item p {
  margin: 0;
  font-family: "Proxima Nova", "Verdana", sans-serif;
  font-size: 14px;
  line-height: 19px;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  padding-left: 20px;
}

.news {
  padding: 30px 0;
}

.news__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(352px, 1fr));
  justify-items: center;
  gap: 24px;
  margin: 40px 0 88px;
}

.news__item {
  background: linear-gradient(0deg, rgba(1, 138, 190, 0.5), rgba(1, 138, 190, 0.5)), url("../img/about-img.webp") no-repeat center/cover;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  width: 100%;
  min-height: 258px;
  padding: 30px 45px 30px 35px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
@supports not (background: linear-gradient(0deg, rgba(1, 138, 190, 0.5), rgba(1, 138, 190, 0.5)), url("../img/about-img.webp") no-repeat center/cover) {
  .news__item {
    background: linear-gradient(0deg, rgba(1, 138, 190, 0.5), rgba(1, 138, 190, 0.5)), url("../img/about-img.png") no-repeat center/cover;
  }
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .news__item {
    background: linear-gradient(0deg, rgba(1, 138, 190, 0.5), rgba(1, 138, 190, 0.5)), url("../img/about-img@2x.webp") no-repeat center/cover;
  }
